@import url(https://fonts.googleapis.com/css2?family=Karla:wght@200;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla&display=swap);
body {
  margin: 0;
  font-family: 'Karla', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Karla', sans-serif;
  background: #000000;
  color:'white';
  margin: auto;
}

.App-header {
  background-color: #000000;
  min-height: 2vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


a:link {
  color: #fffffc;
  text-decoration: none;
}

/* visited link */
a:visited {
  color: #FFFFFC;
}

/* mouse over link */
a:hover {
  color: #c9c8d1;
}

/*!* selected link *!*/
/*a:active {*/
/*  color: #FFFFFC;*/
/*}*/

.box {
  display: flex;
}

.submitIntro{
  font-size: 30px;
}

.waterfallimage {
  width: 600px;
}


.row {
  display: flex;
}

.column1 {
  flex: 30% 1;
  margin: 0px;
}

.column2 {
  flex: 60% 1;
}


.outer {
  overflow: hidden;
  position: relative;
}
.inner {
  position: relative;
  bottom: -50px;
  top: -70px;
}



input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type=text] {
  width: 55%;
  padding: 12px 20px;
  margin: 8px 0;
  font-family: 'Karla', sans-serif;
  color: black;

}


input[type=submit], button {
  padding: 12px 20px;
  margin: 8px 0;
  font-family: 'Karla', sans-serif;
  color: white;
  font-size: 16px;
  background-color: #EE1D52;
  border-radius:10px;
}

textarea:focus, input:focus{
  outline: none;
}

a:hover {
  font-weight: 900;
}

input[type=submit]:hover {
  opacity: 0.8;
}

::-webkit-scrollbar {
  display: none;
}
.hide{
    visibility: hidden;
    display: none;
}

.signup-col {
    padding: 0 50px;
    color: 'white';
    align-content: center;
}
.signup-row {
    padding: 0 20px;
    background-color: white;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(35, 39, 100, .15);
    color: 'white';
    align-content: center;
}
.submit-button {
    padding: 16px 40px;
    font-size: 16px;
    font-weight: bold;
    background-color: 'white';
    color: #000000;
    border: none;
    border-radius: 12px;
    margin: 40px auto 0 auto;
    width: auto;
    display: block;
}

.signup-container {
    width: 50%;
    margin-bottom: 100px;

}
.signup-container p {
    font-size: 24px;
}
.signup-header{
    display: flex;
    width: 40%;
    margin: 0 auto;
    align-items: flex-start;
    color: 'white'
}
.signup-header img {
    width: 20%;
    height: auto;
    margin-right: 20px;
}
.signup-header h1 {
    text-transform: none;
    text-align: left;
}

.signup-form-container {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 25px auto 100px auto;
}

.signup-form {
    width: 100%;
}
#login{
    text-align: center;
    margin: 4em 0;
}

label {
    font-weight: bold;
    font-size: larger;
    color: 'white';
    display:block;
}


input#firstname, input#lastname, input#institution, input#email, input#userkey, input#userval {
    display: block;
    margin-bottom: 25px;
    width: 100%
}

/* input#email, input#institution {
  width: 70%;
} */

.red {
    color: red;
}
.hidden{
    display: none
}
.block {
    display: block
}
#confirmation {
    margin-top: 60px;
    margin-bottom: 100px;
    padding: 25px;
    background: white;
    border-radius: 12px;
}

@media(max-width: 1200px){
    .signup-row{
        padding: 20px 0;
    }
    .signup-header{
        width: 60%
    }
    .signup-header img {
        width:20%;
        height: auto;
    }
    .signup-form-container{
        width: 60%
    }
}


@media(max-width: 700px) {
    .signup-container {
        width: 90%;
        margin: 40px auto 200px auto;
    }

    .signup-container p {
        font-size: 18px;
    }

    .signup-img-col {
        padding: 0;
    }

    .signup-row {
        padding: 20px 0;
    }

    .signup-row {
        padding: 20px 0;
    }

    .signup-header {
        width: 90%
    }

    .signup-header img {
        display: none
    }

    .signup-form-container {
        width: 90%
    }
}

.grid-container {
    display: grid;
    grid-template-columns: auto auto;
    background-color: #2196F3;
    padding: 0px;
}

.roundtop {
    border-collapse: collapse;
    border-top-left-radius: 1em;
    border-top-right-radius: 1em;
    overflow: hidden;
}

.roundbottom {
    border-collapse: collapse;
    border-bottom-left-radius: 1em;
    border-bottom-right-radius: 1em;
    overflow: hidden;
}


